html,body{padding:0;margin:0;font-size:1rem;font-family:'\5FAE\8F6F\96C5\9ED1','\9ED1\4F53';height:100%;}
#app{min-height:100%;height:100%;-webkit-overflow-scrolling:touch; }
.body{background-color:#4fad31;padding-bottom:10px;min-height:100%;}
.head{background-color:#21292c;color:#fff;font-size:1.6rem;text-align:center;padding:10px 0;position:relative;}
.head a{position:absolute;top:20px;left:10px;font-size:1rem;color:#fff;}
.infoBox, .matchBox, .conBox{margin:5px 10px 10px;}
.infoBox{color:#fff;font-size:1rem;padding-top:50px;}
.matchBox header, .conBox header{font-size:1.2rem;text-align:center;font-weight:bold;}
.matchBox{background-color:#fff;padding:5px 10px 10px;}
.matchBox header{margin-bottom:3px;}
.matchDiv{border:2px #000 solid;margin-bottom:10px;}
.matchDiv p,.matchDiv .col-1,.matchDiv .col-2,.matchDiv .col-3,.matchDiv .col-4{text-align:center;}
.matchDiv p{height:35px;line-height:35px;}
.matchDiv .col-1{background-color:#333333;color:#ffe70b;font-weight:bold;height:35px;line-height:35px;}
.matchDiv .col-2{height:33px;line-height:33px;}
.matchDiv .col-3{height:37px;line-height:37px;}
.matchDiv .col-4{min-height:35px;line-height:35px;}
.conBox{background-color:#72bd58;}
.conBox header{height:35px;line-height:35px;}
.conBox header.head-title{color:#ffe70b;padding-top:5px;}
.conBox header.head-title2{background-color:#429129;color:#fff;}
.conBox ol{color:#fff;font-size:1rem;list-style-type:decimal;margin:0;padding:10px 15px 15px 35px;}
.conBox ol li{line-height:34px;}
.weui-cell.moneyInput{display:inline-block;padding:0;width:50px;}
.weui-cell.moneyInput input{border-bottom:1px #333 solid;text-align:center;}
.matchBtn{background-color:#ffe70b;border:0;color:#000;width:100%;height:35px;line-height:35px;font-size:0.9rem;font-weight:bold;}
